}
nav {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.25rem 0;
  background: #018CCD;
}

nav a {
  margin-left: 1rem;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
}

nav a:first-child {
  margin-left: 0;
}

nav a:hover {
  text-decoration: underline;
}

nav a:active {
  color: rgba(255, 255, 255, 1);
}

.container {
  max-width: 70rem;
  width: 90%;
  margin: auto;
}
